<template>
  <div class="text-center" id="app">
    <h1 class="text-secondary">iTranslator Lite</h1>
    <h5 class="text-success">Simple and Quick Translator</h5>
    <hr>
    <TranslateForm v-on:formSubmit="translateText"></TranslateForm>
    <br>
    <TranslateOutput v-text="translatedText"></TranslateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput';

export default {
  name: 'app',
  components:{
    TranslateForm,
    TranslateOutput
  },
  data: function(){
    return {
      translatedText:''
    }
  },
  methods: {
    translateText:function(text, language){
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20190511T071308Z.48b7fcea91de7328.040b375c79bdac6ccc73d2a6539c3750b3cb7cf6&lang='+language+'&text='+text)
      .then((response) => {
        this.translatedText = response.body.text[0];
      });
    }
  }
}
</script>

<style>
body {
  background:#fefefe;
  text-align: center;
  padding: 2%;
}
</style>



