<template>
  <div class="row" id="translateForm">
    <div class="col-lg-6 col-md-offset-3">
      <form id="transForm" class="form-inline well" v-on:submit="formSubmit">
        <input class="form-control" type="text" v-model="textToTranslate" placeholder="Enter a Text...">
        <br>
        <select class="form-control" v-model="language">
          <option value="de">German</option>
          <option value="en">English</option>
          <option value="ar">Arabic</option>
          <option value="bs">Bosnian</option>
          <option value="zh">Chinese</option>
          <option value="cs">Czech</option>
          <option value="nl">Dutch</option>
          <option value="fr">French</option>
          <option value="fl">Finnish</option>
          <option value="hi">Hindi</option>
          <option value="hu">Hungarian</option>
          <option value="it">Italian</option>
          <option value="ja">Japanese</option>
          <option value="la">Latin</option>
          <option value="lv">Latvian</option>
          <option value="ms">Malay</option>
          <option value="no">Norwegian</option>
          <option value="pl">Polish</option>
          <option value="pt">Portuguese</option>
          <option value="sk">Slovakian</option>
          <option value="es">Spanish</option>
          <option value="sv">Swedish</option>
          <option value="tr">Turkish</option>
          <option value="ur">Urdu</option>
        </select>
        <br><br>
        <input class="btn btn-primary btn-lg btn-block" type="submit" value="Translate">
        <br><br>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'translateForm',
  data(){
    return {
      textToTranslate:'',
      language:''
    }
  },
  created() {
    this.language = 'de';
  },
  methods: {
    formSubmit(e){
      this.$emit('formSubmit', this.textToTranslate, this.language);
      e.preventDefault();
    }
  }
}
</script>

<style>
  #transForm{
    border-radius: 15px;
  }
</style>



